import { Component, OnInit } from '@angular/core';
import { MastersServicesApi } from "../util/services/masters.api";
import { PageModel } from '../page.model';
import { NgbDateStruct, NgbCalendar } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-modemdetails',
  templateUrl: './modemdetails.component.html',
  styleUrls: ['./modemdetails.component.css']
})
export class ModemdetailsComponent implements OnInit {

  public page: PageModel;
  public p: number;
  searchValue = '';
  private selected: string;
  model: NgbDateStruct;
  public showSearchClear = false;

  constructor(private mastersServicesApi:MastersServicesApi, private calendar: NgbCalendar) {
    this.page = new PageModel(0, 0, 0, 0);
    // this.model = this.calendar.getToday();
    this.selected = '';
   }
  modemdetails;
  ngOnInit() {
    this.getallmodemdetails(0, this.searchValue, this.selected);
  }

  pageChanged(event) {
    this.p = event;
    this.getallmodemdetails(this.p - 1, this.searchValue, this.selected); // need to remove param
  }
  changeDate() {
    this.selected = this.model.day + '/' + this.model.month + '/' + this.model.year;
    this.getallmodemdetails(0, this.searchValue, this.selected); // need to remove param
  }

  searchData(){
    if (this.searchValue !== undefined) {
      // this.selected = this.model.day + '/' + this.model.month + '/' + this.model.year;
      this.getallmodemdetails(0, this.searchValue, this.selected);
      this.showSearchClear = (this.searchValue.length > 4) ? true : false;
    }
  }

  clearSearch() {
    this.searchValue = '';
    this.showSearchClear = false;
    this.searchData();
  }

  getallmodemdetails(page: number, searchString: string, searchDate: string){
    // this.selected = this.model.day + '/' + this.model.month + '/' + this.model.year;
    this.mastersServicesApi.getModemData(page, 10, searchString, searchDate).subscribe((data:any) => {
      this.modemdetails  =  data.content;
      this.page = data.page;
      console.log(this.modemdetails);
    });
  }

}
